form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
    border-radius: 25px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 39px;
    margin-bottom: 26px;
    color: black;
  }
  
  textarea {
    font-family: 'Josefin Sans', sans-serif;
    background-color:whitesmoke;
    text-align: center;
    border-radius: 50px;
    box-shadow: 0px 10px 10px rgba(27, 34, 49, 15%);
    display: flex;
    width: 280px;
    height: 30px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    border: 0px;
    outline-color: rgb(146, 145, 145);
    align-content: space-evenly;
    font-size: 19px;
}
 
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 200;
  text-align: center;
  }
  
  textarea:focus {
    outline-color: #abbac7;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
 
    font-family: 'Josefin Sans', sans-serif;
  }
  
  .TodoForm-button-add {
    font-family: 'Josefin Sans', sans-serif;
    display: flex;
    background-color: #69fa61;
    box-shadow: 0px 5px 25px rgba(97, 250, 168, 0.5);
    border-radius: 20px;
    border: none;
    width: 107px;
    height: 50px;
    align-items: center;
    font-size: 17px;
    
  }
  
  .TodoForm-button-cancel {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 21px;
    background: transparent;
    border-radius: 5px;
    border: none;
  }
  